                            // .contact
                            header {
                                height: inherit;
                            }
                            
                            .contact {
                                text-align: right;
                                h2 {
                                    @extend %head;
                                    font-size: 20px;
                                    @include Xs {
                                        margin-bottom: 20px;
                                    }
                                }
                                p {
                                    @extend %p;
                                }
                                .search {
                                    display: flex;
                                    justify-content: space-between;
                                    @include Sm {
                                        display: block;
                                    }
                                    form {
                                        text-align: right;
                                        width: 50%;
                                        margin: auto;
                                        @include Sm {
                                            width: 80%;
                                        }
                                        div {
                                            margin: 20px 0px;
                                        }
                                    }
                                    // contact-us
                                    .contact-us {
                                        padding-right: 30px;
                                        ul {
                                            margin-top: 20px;
                                            li {
                                                display: flex;
                                                i {
                                                    color: $main-color;
                                                    margin-top: 4px;
                                                    font-size: 18px;
                                                }
                                                .cont {
                                                    margin-right: 14px;
                                                    margin-bottom: 20px;
                                                    h3 {
                                                        font-size: 18px;
                                                        margin-bottom: 8px;
                                                        @include font-family( 'fr-bold-65');
                                                        @include Sm {
                                                            font-size: 16px;
                                                        }
                                                    }
                                                    p,
                                                    span {
                                                        margin-bottom: 0;
                                                        @extend %p;
                                                        @include font-family( "fr-Roman-55");
                                                        @include Sm {
                                                            font-size: 14px;
                                                        }
                                                    }
                                                }
                                            }
                                        }
                                    }
                                    button.link {
                                        width: 50%;
                                        margin-right: auto;
                                        background-color: $main-color;
                                        border-radius: 100px;
                                        color: $white;
                                        height: 50px;
                                        line-height: 50px;
                                        font-family: "fr-bold-65";
                                        font-size: 20px;
                                        margin-bottom: 70px;
                                        @include Sm {
                                            margin-bottom: 40px;
                                            display: block;
                                            width: 100%;
                                        }
                                    }
                                }
                                textarea {
                                    height: 150px!important;
                                    border-radius: 20px;
                                }
                            }