     // careers
     .careers {
         text-align: center;
         h2 {
             @extend %head;
             margin: 40px 0px;
             @include font-family( 'fr-bold-65');
             @include Xs {
                 margin: 10px 0px 20px;
             }
         }
         p {
             @extend %p;
             margin-bottom: 40px;
         }
         // form
         form {
             text-align: right;
             width: 50%;
             margin: auto;
             @include Sm {
                 width: 80%;
                 input {
                     @include Xs {
                         height: 45px;
                     }
                 }
             }
             div {
                 margin: 20px 0px;
             }
             .custom-file {
                 position: relative;
                 height: 100%;
                 margin: 0;
                 input[type="file"] {
                     position: absolute;
                     left: 0px;
                     top: 0px;
                     height: 100%!important;
                     overflow: hidden;
                     width: 25%;
                     opacity: 0;
                     z-index: 2;
                 }
                 span {
                     position: absolute;
                     left: 2%;
                     top: 20%;
                     width: 120px;
                     height: 30px;
                     background-color: #dedede;
                     color: #8a8a8a;
                     text-align: center;
                     line-height: 30px;
                     border-radius: 100px;
                     @include Sm {
                         width: 100px;
                         line-height: 25px;
                     }
                 }
             }
             textarea {
                 height: 150px!important;
                 @include border-radius(30px);
             }
         }
         button {
             width: 100%;
             background-color: $main-color;
             color: $white;
             height: 50px;
             line-height: 50px;
             font-family: "fr-bold-65";
             font-size: 20px;
             margin-bottom: 70px;
             cursor: pointer;
             @include Sm {
                 margin-bottom: 35px;
             }
         }
     }