// footer
footer {
    background-image: linear-gradient(to bottom, #01044a, #137cb0);
    @include Sm {
        padding: 60px 0px;
    }
    @include Md {
        padding: 50px 0px;
    }
    @include Lg {
        padding: 70px 0px;
    }
    .all {
        @extend %flex;
        align-items: flex-start;
        flex-flow: wrap;
        a {
            @extend %head;
            color: $white;
            font-size: 20px;
            margin: 10px 0px;
            @include Sm {
                font-size: 14px;
            }
        }
        .links {
            padding: 0px 10px;
        }
        .plus {
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            @include Xs {
                margin-bottom: 10px;
            }
            span {
                &#plus-one.active::after {
                    content: "\f068";
                    font-family: "Font Awesome 5 free";
                    font-weight: 900;
                    color: $white;
                }
            }
        }
        .links {
            ul {
                transition: all .5s ease-in-out;
                display: block;
                width: 100%;
                @include Sm {
                    display: none;
                    &.open {
                        display: block;
                    }
                }
                li {
                    margin: 15px 0px;
                    a {
                        font-size: 14px;
                        color: $par;
                        font-family: 'fr-bold-65';
                        @extend %trans;
                        color: #989393;
                        &:hover {
                            color: #afafaf;
                        }
                    }
                }
            }
        }
        // contact-us
        .contact-us {
            #plus {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 0px 10px;
            }
            span {
                &#plus-two.two::after {
                    content: "\f068";
                    font-family: "Font Awesome 5 free";
                    font-weight: 900;
                    color: $white;
                }
            }
            ul {
                display: block;
                transition: all .6s ease-in-out;
                @include Sm {
                    display: none;
                    &.open {
                        display: block;
                    }
                }
                li {
                    display: flex;
                    i {
                        color: $main-color;
                        line-height: 46px;
                    }
                    a {
                        margin-right: 14px;
                        margin-bottom: 20px;
                        p {
                            margin-bottom: 0;
                            font-size: 16px;
                            color: #989393;
                            @include font-family('fr-Light-45');
                        }
                    }
                }
            }
        }
        // news
        .news {
            line-height: 2;
            @include Sm {
                width: 100%;
                margin: auto;
                text-align: center;
                line-height: 2;
            }
            @include mob425 {
                margin-top: 20px;
            }
            p {
                font-size: 16px;
                color: $white;
                @include font-family('fr-Light-45');
                @include Xs {
                    font-size: 14px;
                }
            }
            form {
                position: relative;
                @include Sm {
                    width: 50%;
                    margin: auto;
                }
                @include Xs {
                    width: 80%;
                    margin-top: 30px;
                }
                input {
                    width: 100%;
                    height: 40px;
                    border-radius: 100px;
                    border: none;
                    outline: none;
                    padding-right: 20px;
                }
                button {
                    position: absolute;
                    left: 10px;
                    top: 50%;
                    transform: translateY(-50%);
                    border: none;
                    background: $main-color;
                    border-radius: 50%;
                    color: $white;
                    cursor: pointer;
                    width: 30px;
                    height: 30px;
                    line-height: 30px;
                    &:focus {
                        @extend %outline;
                        background-color: rgba(250, 93, 30, 0.1);
                    }
                    &::placeholder {
                        padding-right: 10px;
                        color: #989393;
                    }
                }
            }
        }
    }
}