// upper-bars
.upper-bar {
    padding: 10px 0px;
    background-image: linear-gradient(to bottom, #01044a, #137cb0);
    .up {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .right {
            span {
                color: $white;
                @include mob425 {
                    font-size: 12px;
                }
            }
        }
        .left {
            a {
                margin-left: 15px;
                color: $white;
                @include mob425 {
                    margin-left: 10px;
                }
            }
        }
    }
}

// upper-bar
//    navbar
.nav-bar {
    padding: 15px 0px;
    margin-bottom: 10px;
    .navs {
        @extend %flex;
        .logo {
            width: 250px;
            height: 105px;
            @include mob425 {
                overflow: hidden;
            }
            img {
                object-fit: contain;
                width: 100%;
                height: 100%;
            }
        }
        .slide-bar {
            display: flex;
            align-items: center;
            justify-content: space-between;
            @extend %trans;
            @media(max-width: 991px) {
                & {
                    position: fixed;
                    height: 100%;
                    width: 100%;
                    background: #333;
                    right: 0;
                    top: 0;
                    z-index: 999999;
                    transform: scaleX(0);
                    transform-origin: right;
                }
                &.active {
                    transform: scaleX(1);
                    transform-origin: left;
                }
            }
            // nav
            nav {
                @media(max-width: 991px) {
                    & {
                        position: absolute;
                        left: 50%;
                        top: 50%;
                        transform: translate(-50%, -50%);
                    }
                }
                ul {
                    display: flex;
                    margin-bottom: 0;
                    @media(max-width: 991px) {
                        & {
                            display: block;
                        }
                    }
                    li {
                        margin-left: 12px;
                        padding: 10px;
                        &:hover {
                            background-color: $main-color;
                        }
                        @include Lg {
                            margin-left: 25px!important;
                        }
                        @include Sm {
                            margin-bottom: 10px;
                        }
                        a {
                            text-decoration: none;
                            font-size: 14px;
                            color: $white;
                            font-family: 'fr-bold-65';
                            @extend %trans;
                            @include Sm {
                                color: $white;
                                font-size: 16px;
                            }
                        }
                    }
                    .part {
                        display: none;
                        position: absolute;
                        margin-top: 20px;
                        margin-left: -50px;
                        ul {
                            display: block;
                            background-image: linear-gradient(to bottom, #01044a, #137cb0);
                            li {
                                margin-left: 0;
                            }
                        }
                    }
                    .part.active {
                        display: block;
                    }
                }
            }
            // nav
            // search-bar
            .search-bar {
                position: relative;
                input {
                    width: 240px;
                    height: 42px;
                    border: 1px solid $blue;
                    border-radius: 100px;
                    background-color: $blue;
                    padding-right: 10px;
                    @include Md {
                        width: 200px;
                    }
                    &:focus {
                        @extend %outline;
                        background-color: rgba(250, 93, 30, 0.1);
                    }
                    &::placeholder {
                        padding-right: 10px;
                        color: #afafaf;
                    }
                }
                button {
                    position: absolute;
                    left: 10px;
                    top: 50%;
                    transform: translateY(-50%);
                    width: 30px;
                    height: 30px;
                    line-height: 12px;
                    border: none;
                    background: $main-color;
                    border-radius: 50%;
                    color: $white;
                    i {
                        font-size: 12px;
                    }
                }
            }
        }
    }
}

.nav-bar .toggle {
    cursor: pointer;
    display: none;
}

.close-tab {
    display: none;
}

.close-search {
    display: none;
}

i.close-times {
    display: none;
}

// mobile
@media(max-width:991px) {
    .toggle {
        width: 40px;
        height: 40px;
        position: relative;
        display: block!important;
        background-color: $main-color;
        border-radius: 50%;
        position: fixed;
        z-index: 100000;
        left: 15px;
        &.close {
            display: none;
        }
        i {
            font-size: 20px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            color: $white;
        }
    }
    .close-tab,
    .close-search {
        display: block;
        position: absolute;
        top: 40px;
        z-index: 99999;
        width: 30px;
        height: 30px;
        text-align: center;
        background: $main-color;
        line-height: 30px;
        border-radius: 50%;
        cursor: pointer;
        transition: all .5s ease-in-out;
        opacity: 1;
        i {
            color: $white;
            font-size: 14px;
        }
    }
    .close-tab {
        opacity: 0;
        left: 40px;
        &.open {
            display: block;
            opacity: 1;
        }
    }
    .close-search {
        right: 40px;
    }
    .search-bar {
        form {
            position: fixed;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            opacity: 0;
            visibility: hidden;
            transition: all .5s ease-in-out;
            background-color: rgba(61, 61, 61, 0.98);
            z-index: 99999999;
            display: flex;
            justify-content: center;
            align-items: center;
            &.open {
                opacity: 1;
                visibility: visible;
            }
            .search {
                width: 70%;
                position: relative;
                transform: scaleX(0);
                &.open {
                    transform: scaleX(1.2);
                    transition: all .5s ease-in-out;
                }
                input {
                    width: 100%!important;
                    color: $white;
                }
            }
            i.close-times {
                position: absolute;
                top: 10%;
                color: $white;
                font-size: 20px;
                background: $main-color;
                width: 30px;
                height: 30px;
                line-height: 30px!important;
                text-align: center;
                border-radius: 50%;
                cursor: pointer;
                display: block;
                font-size: 14px;
            }
        }
    }
}