// navbar
.nav-bar {
    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
}

// .single
.single {
    text-align: center;
    h2 {
        @include font-family( 'fr-bold-65');
        @extend %head;
        font-size: 20px;
        margin-bottom: 20px;
        &::after {
            content: none;
        }
    }
    img {
        margin-bottom: 40px;
    }
    p {
        margin-bottom: 120px;
        @extend %p;
        line-height: 2.2;
        @include Sm {
            margin-bottom: 50px;
        }
        @include mob425 {
            margin-bottom: 30px;
        }
    }
}