a:link,
input,
input:focus,
button,
button:focus {
    text-decoration: none;
    outline: none;
}

button {
    cursor: pointer;
    border: none;
    &:focus {
        border: none;
        outline: none;
    }
}

p {
    @include Xs {
        font-size: 14px!important;
    }
}

// // .breads-crumb
.breads-crumb {
    margin-top: 20px;
    padding: 20px 0px;
    ol.breadcrumb {
        border-radius: 100px;
        padding-right: 30px;
        a {
            color: $breads-crumb;
            @include Xs {
                font-size: 14px;
            }
        }
    }
}

// brear-crumb
// form
label {
    font-size: 15px;
    @include font-family('fr-bold-65');
    &::after {
        content: "*";
        color: $main-color;
    }
}

.form-control {
    @extend %outline;
    height: 50px;
    @include border-radius(100px);
    padding-right: 20px;
    &:focus {
        @extend %outline;
        box-shadow: none;
        border: 1px solid $gray;
    }
}

// // 
.card {
    border: none;
    @extend %trans;
    .card-head {
        overflow: hidden;
        img {
            @extend %trans;
        }
    }
    .card-body {
        a {
            text-decoration: none;
            color: $par;
            font-size: 16px;
            margin-top: 20px;
            @include font-family('fr-Light-45');
            @extend %trans;
        }
    }
    &:hover a {
        color: $main-color;
    }
}

.link {
    color: $link;
    background-color: $blue;
    width: 140px;
    height: 40px;
    line-height: 40px;
    display: block;
    margin: 50px auto 0;
    @include border-radius(100px);
    @include font-family('fr-Light-45');
    @extend %trans;
    &:hover {
        background-color: $main-color;
        color: $white;
    }
    @include Sm {
        margin: 10px auto 0;
    }
}

footer {
    span {
        display: none;
        @include Sm {
            display: block;
            position: relative;
            left: 0;
            top: 0;
            font-size: 18px;
            cursor: pointer;
            font-size: 14px;
            transition: all .5s ease-in-out;
            &::after {
                position: relative;
                left: 0;
                top: 0;
                content: "\f067";
                font-family: "Font Awesome 5 free";
                font-weight: 900;
                color: $white;
                transition: all .5s ease-in-out;
            }
        }
    }
    .contact-us,
    .news,
    .links {
        @include Sm {
            width: 100%;
        }
    }
}