// client
.client {
    text-align: center;
    padding: 40px 0px;
    h2 {
        @include font-family( 'fr-bold-65');
        @extend %head;
        font-size: 20px;
        margin-bottom: 30px;
    }
    .images {
        @extend %flex;
        flex-flow: wrap;
    }
    .card {
        width: calc((100% - 60px) / 4);
        @include Sm {
            margin-top: 10px;
            height: 280px;
            width: 30.33%;
            margin: 0% 1%;
        }
        @include Xs {
            width: 48%;
            margin: 0% 1%;
        }
        @include mob480 {
            width: 70%;
            margin: auto;
        }
        margin-top: 10px;
        height: 280px;
        .card-head {
            @extend %flex;
            width: 100%;
            height: 217px;
            justify-content: center;
            border: 1px solid $breads-crumb;
            overflow: hidden;
            img {
                width: inherit;
                height: 100%;
                max-width: 100%;
            }
        }
        .card-body {
            padding: 10px 0;
            font-size: 16px;
            margin-bottom: 0;
            text-align: center;
            a {
                color: $black;
                @include Sm {
                    font-size: 14px;
                }
            }
        }
        &:hover .card-head {
            @extend %trans;
            border: 1px solid $main-color;
        }
        &:hover a {
            color: $main-color;
        }
        a.link {
            margin-bottom: 100px;
        }
    }
}