$main-color:#01044a;
$par:#3f3f3f;
$header:#3c3c3c;
$white:#fff;
$last-color:#fabea5;
$blue:#f6f6f6;
$pargraph:#afafaf;
$link:#afafaf;
$black:#000;
$breads-crumb:#a5a5a5;
$gray:#999;
// outline
%outline {
    outline: none;
}

%trans {
    transition: all .5s ease-in-out;
}

%head {
    color: $header;
    font-size: 24px;
    font-family: "fr-Black-75";
    margin-bottom: 40px;
}

%flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

//  border-radius
%border-radius {
    border-radius: 50%;
}

%p {
    color: $par;
    font-family: 'fr-Light-45';
    font-size: 16px;
}