                // header
                header {
                    // slider
                    background-image: linear-gradient(to bottom, #01044a, #137cb0);
                    height: 100vh;
                    h2 {
                        font-family: 'fr-bold-65';
                        font-size: 35px;
                        margin-top: 30px;
                        color: #fff;
                        @include Xs {
                            margin-top: 15px;
                            font-size: 25px;
                        }
                    }
                    .slide {
                        .item {
                            a {
                                height: 250px;
                                margin-top: 50px;
                                border-radius: 20px;
                                display: inline-block;
                                @include Xs {
                                    margin-top: 25px;
                                }
                                img {
                                    width: 100%;
                                    height: 100%;
                                    border-radius: 10px;
                                    // object-fit: cover;
                                }
                            }
                            h3 {
                                font-family: 'fr-bold-65';
                                font-size: 17px;
                                margin-top: 30px;
                                color: #fff;
                                text-align: center;
                            }
                        }
                    }
                }
                
                // slider
                // contant
                .contant {
                    background-image: linear-gradient(to bottom, #137cb0, #01044a);
                    padding: 50px 0;
                    h2 {
                        font-family: 'fr-bold-65';
                        font-size: 35px;
                        margin-top: 30px;
                        color: #fff;
                        @include Xs {
                            margin-top: 15px;
                            font-size: 25px;
                        }
                    }
                    .cont {
                        display: flex;
                        align-items: flex-start;
                        justify-content: space-between;
                        @include Xs {
                            display: inline-flex;
                            flex-direction: column-reverse;
                        }
                        @include mob560 {
                            flex-direction: column-reverse;
                        }
                    }
                }
                
                .one {
                    width: 30%;
                    display: flex;
                    @include Xs {
                        width: 100%;
                    }
                }
                
                .video {
                    width: 100%;
                    .card-video {
                        margin: 35px 0px 0px;
                        @include Xs {
                            display: flex;
                            flex-flow: wrap;
                            justify-content: space-between;
                        }
                        .card {
                            border: none;
                            position: relative;
                            margin-bottom: 20px;
                            width: 100%;
                            @include Xs {
                                width: 250px;
                            }
                            @include mob560 {
                                width: 70%;
                                margin: 10px auto;
                            }
                            .fade-out {
                                position: absolute;
                                left: 50%;
                                top: 40%;
                                transform: translate(-50%, -40%);
                                background: $white;
                                text-align: center;
                                border-radius: 50%;
                                ul {
                                    margin: 0;
                                    padding: 0;
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    li {
                                        width: 50px;
                                        height: 50px;
                                        line-height: 50px;
                                    }
                                    i,
                                    a {
                                        color: $main-color;
                                        font-size: 16px;
                                    }
                                }
                            }
                            .img-video {
                                background-repeat: no-repeat;
                                height: 230px;
                                border-radius: 10px;
                                @include Xs {
                                    height: 280px;
                                }
                            }
                            p {
                                font-size: 16px;
                                margin: 10px 0px 25px;
                            }
                        }
                    }
                    .watch {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        a {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            margin: 0 5px!important;
                        }
                        p {
                            color: #3f3f3f;
                            font-size: 16px;
                            font-family: "fr-Light-45";
                            margin: 0px 5px!important;
                        }
                    }
                }
                
                // img
                .img {
                    height: 280px;
                }
                
                .img-video {
                    background-size: cover;
                }
                
                .img-video-1 {
                    background-image: url(../images/10.png);
                }
                
                .img-video-2 {
                    background-image: url(../images/6.png);
                }
                
                .img-video-3 {
                    background-image: url(../images/7.png);
                }
                
                // .img-video-4 {}
                // video
                .two {
                    width: 50%;
                    @include Xs {
                        width: 100%;
                    }
                    .card {
                        width: 100%!important;
                    }
                    a.img-video.img-video-4 {
                        height: 300px!important;
                        background-image: url(../images/3.png);
                    }
                }